/* ------------------------------------------------------------------ */
/* a. General Styles
/* ------------------------------------------------------------------ */
body {
  background: #0f0f0f;
}
/* ------------------------------------------------------------------ */
/* b. Header Styles
/* ------------------------------------------------------------------ */
header {
  position: relative;
  height: 800px;
  min-height: 500px;
  width: 100%;
  background: #161415 url(/static/img/header-background.d5f69d2.jpg) no-repeat top center;
  background-size: cover !important;
  -webkit-background-size: cover !important;
  text-align: center;
  overflow: hidden;
}
/* vertically center banner section */
header:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}
header .banner {
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  width: 85%;
  padding-bottom: 30px;
  text-align: center;
}
header .banner-text {
  width: 100%;
}
header .banner-text h1 {
  font: bold 90px/1.1em 'Fira Sans', sans-serif;
  color: #fff;
  letter-spacing: -2px;
  margin: 0 auto 18px auto;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.8);
}
header .banner-text h1 span {
    font-size: .75em;
}
header .banner-text h3 {
  font: 18px/1.9em 'Montserrat', sans-serif;
  color: #fff;
  margin: 0 auto;
  width: 70%;
